import { MachineNodeModel } from './layout/Node';
import * as WebMidi from 'webmidi';
import { MidiLinkModel } from './layout/Link';

export interface MachineMessage {

	type: string, message: {

		rawData: Uint8Array,
		isChannelMessage: boolean,
		type: string,
		channel: number
	}
}

export interface Machine {

	buildNode(): MachineNodeModel;
	getFactory(): MachineFactory;
	getId(): string;
}

export abstract class AbstractMachine implements Machine {

	private static counters: { [machineName: string]: number } = {};
	abstract buildNode(): MachineNodeModel;
	abstract getFactory(): MachineFactory;
	protected readonly id: string;
	protected constructor() {

		if (!AbstractMachine.counters[this.constructor.name]) {

			AbstractMachine.counters[this.constructor.name] = 0;
		}

		this.id = this.constructor.name + "_" + AbstractMachine.counters[this.constructor.name]++;
	}

	getId() {

		return this.id;
	}
}

export interface MachineSource extends Machine {

	setEmit(emit: (messageEvent: MachineMessage, channel: number) => void): void;
	getOutChannelCount(): number;
}

export interface MachineTarget extends Machine {

	receive(messageEvent: MachineMessage, channel: number, link: MidiLinkModel): void;
	getInChannelCount(): number;
}

export interface MachineSourceTarget extends MachineSource, MachineTarget { }

export class MachineRoutings {

	private readonly routings: {
		[machineSource: string]: { [machineTarget: string]: Function[] }[]
	};

	private readonly deletions: {
		[linkId: string]: Function
	};

	constructor() {

		this.routings = {};
		this.deletions = {};
	}

	connect(source: MachineSource,
		target: MachineTarget,
		fromChannel: number,
		toChannel: number,
		link: MidiLinkModel) {

		if (!this.routings[source.getId()]) {

			this.routings[source.getId()] = [];
		}

		if (!this.routings[source.getId()][fromChannel - 1]) {

			this.routings[source.getId()][fromChannel - 1] = {};
		}

		if (!this.routings[source.getId()][fromChannel - 1][target.getId()]) {

			this.routings[source.getId()][fromChannel - 1][target.getId()] = [];
		}
		
		const callback = (e: MachineMessage) => {

			target.receive(e, toChannel, link);
		}

		const emit: (messageEvent: MachineMessage, channel: number) => void = (e: MachineMessage, channel: number) => {

			Object.values(this.routings[source.getId()][channel - 1] ?? []).forEach(handler => handler.forEach(h => h(e, channel)));
		};

		source.setEmit(emit);

		this.routings[source.getId()][fromChannel - 1][target.getId()][toChannel - 1] = callback;

		this.deletions[link.getID()] = () => delete this.routings[source.getId()][fromChannel - 1][target.getId()][toChannel - 1];
	}

	disconnect(link: MidiLinkModel) {

		this.deletions[link.getID()]();
		delete this.deletions[link.getID()];
	}
}

export interface MachineFactory {

	createMachine(): AbstractMachine;
	getName(): string;
	getColor(): string;
	getRequestRefresh(): Function;
}

export class MidiMachineSource extends AbstractMachine implements MachineSource {

	private readonly factory: MachineFactory;
	getFactory() { return this.factory; }
	static buildFactory(refresh: Function,
		midiInput: WebMidi.Input,
		...signals: (keyof WebMidi.InputEventMap)[]): MachineFactory {

		const factory = {

			createMachine() { return new MidiMachineSource(this, midiInput, ...signals); },
			getName() { return midiInput.name + " (in)"; },
			getColor() { return "rgb(150,150,50)"; },
			getRequestRefresh() { return refresh; }
		};

		return factory;
	}

	readonly midiInput: WebMidi.Input;
	private emit?: (messageEvent: MachineMessage, channel: number) => void;
	setEmit(emit: (messageEvent: MachineMessage, channel: number) => void) { this.emit = emit; }

	private constructor(factory: MachineFactory, midiInput: WebMidi.Input, ...signals: (keyof WebMidi.InputEventMap)[]) {

		super();
		this.factory = factory;
		this.midiInput = midiInput;

		signals.forEach(signal => {
			this.midiInput.addListener(signal, (e: WebMidi.MessageEvent) => {

				this.emit?.(e, e.message.channel);
			});
		});
	}

	getOutChannelCount() { return this.midiInput.channels.length; }

	buildNode(): MachineNodeModel {

		const node = new MachineNodeModel(this.getFactory().getRequestRefresh(), this);

		node.addMachineOutPort("All", 0);
		for (let i = 0; i < this.getOutChannelCount(); i++) {

			node.addMachineOutPort("Channel " + (i + 1), i + 1);
		}

		return node;
	}
}

export class MidiMachineTarget extends AbstractMachine implements MachineTarget {

	private readonly factory: MachineFactory;
	getFactory() { return this.factory; }

	readonly midiOutput: WebMidi.Output;
	static buildFactory(requestRefresh: Function, midiOutput: WebMidi.Output): MachineFactory {

		const factory =  {

			createMachine() { return new MidiMachineTarget(factory, midiOutput); },
			getName() { return midiOutput.name + " (out)"; },
			getColor() { return "rgb(150,150,50)"; },
			getRequestRefresh() { return requestRefresh }
		}

		return factory;
	}

	private constructor(factory: MachineFactory, midiOutput: WebMidi.Output) {

		super();
		this.factory = factory;
		this.midiOutput = midiOutput;
	}

	getInChannelCount() { return this.midiOutput.channels.length; }

	receive(messageEvent: MachineMessage, channel: number, link: MidiLinkModel) {

		link.setSending(true);

		console.log(messageEvent.message.rawData);
		if (messageEvent.message.isChannelMessage) {

			messageEvent.message.rawData[0] >>= 4;
			messageEvent.message.rawData[0] <<= 4;
			messageEvent.message.rawData[0] |= (channel - 1); // override channel
        }

		this.midiOutput.send(messageEvent.message.rawData);
	}

	buildNode(): MachineNodeModel {

		const node = new MachineNodeModel(this.getFactory().getRequestRefresh(), this);

		node.addMachineInPort("Input Wide", 0);
		for (let i = 0; i < this.getInChannelCount(); i++) {

			node.addMachineInPort("Channel " + (i + 1), i + 1);
		}

		return node;
	}
}

export class ThruMachine extends AbstractMachine implements MachineSourceTarget {

	private static factory: MachineFactory;
	getFactory() { return ThruMachine.factory; }
	getOutChannelCount(): number { return 1 }
	getInChannelCount(): number { return 1 }
	private emit?: (messageEvent: MachineMessage, channel: number) => void;
	setEmit(emit: (messageEvent: MachineMessage, channel: number) => void) { this.emit = emit; }

	static buildFactory(requestRefresh: Function): MachineFactory {

		if (this.factory) {

			return this.factory;
		}

		this.factory = {

			createMachine() { return new ThruMachine(); },
			getColor() { return "rgb(0,150,255)"; },
			getName() { return ThruMachine.name; },
			getRequestRefresh() { return requestRefresh }
		}

		return this.factory;
	}

	private readonly signals: (keyof WebMidi.InputEventMap)[];
	private constructor(...signals: (keyof WebMidi.InputEventMap)[]) {

		super();
		this.signals = signals;
	}

	buildNode(): MachineNodeModel {

		const node = new MachineNodeModel(this.getFactory().getRequestRefresh(), this);

		node.addMachineOutPort("Channel 1", 1);
		node.addMachineInPort("In", 1);
		return node;
	}

	receive(messageEvent: MachineMessage, channel: number, link: MidiLinkModel) {

		link.setSending(true);
		this.emit?.(messageEvent, channel);
	}
}

export class NoteRoundRobinMachine extends AbstractMachine implements MachineSourceTarget {

	private static factory: MachineFactory;
	getFactory() { return NoteRoundRobinMachine.factory; }

	getOutChannelCount(): number { return this.voices }
	getInChannelCount(): number { return 1 }

	private readonly activeVoices: { [note: number]: number };
	private readonly voices: number;
	private currentVoice: number;
	private emit?: (messageEvent: MachineMessage, channel: number) => void;
	setEmit(emit: (messageEvent: MachineMessage, channel: number) => void) { this.emit = emit; }

	static buildFactory(requestRefresh: Function): MachineFactory {

		if (this.factory) {

			return this.factory;
		}

		this.factory = {

			createMachine() {
				return new NoteRoundRobinMachine(Number(window.prompt("How many voices?", "8")));
			},
			getColor() { return "rgb(0,150,255)"; },
			getName() { return NoteRoundRobinMachine.name; },
			getRequestRefresh() { return requestRefresh }
		}

		return this.factory;
	}

	private constructor(voices: number) {

		super();
		this.voices = voices;
		this.currentVoice = 0;
		this.activeVoices = {};
    }

	receive(messageEvent: MachineMessage, _: number, link: MidiLinkModel) {

		if (messageEvent.message.type === "noteoff" ||
			(messageEvent.message.type === "noteon" && messageEvent.message.rawData[2] === 0)) {

			link.setSending(true);
			if (this.activeVoices[messageEvent.message.rawData[1]] != undefined) {

				this.emit?.(messageEvent, this.activeVoices[messageEvent.message.rawData[1]] + 1);
			}
		}
		else if (messageEvent.message.type === "noteon") {

			link.setSending(true);
			this.activeVoices[messageEvent.message.rawData[1]] = this.currentVoice;
			this.emit?.(messageEvent, this.currentVoice + 1);
			this.currentVoice = (this.currentVoice + 1) % this.getOutChannelCount();
		} 
    }

	buildNode(): MachineNodeModel {

		const node = new MachineNodeModel(this.getFactory().getRequestRefresh(), this);
		node.addMachineOutPort("All", 0);
		for (let i = 0; i < this.getOutChannelCount(); i++) {

			node.addMachineOutPort("Channel " + (i + 1), i + 1);
		}

		node.addMachineInPort("In", 1);
		return node;
	}
}