import React from 'react';

import styled from '@emotion/styled';
import { CanvasWidget, BaseEntityEvent, BaseEvent, BaseEntity, BaseEntityGenerics } from '@projectstorm/react-canvas-core';
import createEngine, { LinkModel, DiagramModel, DefaultNodeModel, DefaultPortModel, LinkModelGenerics } from '@projectstorm/react-diagrams';
import * as WebMidi from 'webmidi';

import { WorkspaceButton, Workspace } from './layout/Workspace';
import { Canvas } from './layout/Canvas';
import { Tray } from './layout/Tray';
import { TrayItem } from './layout/TrayItem';
import { MachineSource, MachineTarget, MidiMachineSource, MidiMachineTarget, ThruMachine, NoteRoundRobinMachine, MachineFactory, MachineRoutings } from './Machines';
import { MachineNodeModel, MachinePortModel } from './layout/Node';
import { MidiLinkFactory, MidiLinkModel } from './layout/Link';
import { KeyboardMachine, KeyboardNodeFactory } from './KeyboardMachine';

const engine = createEngine();
engine.getNodeFactories().registerFactory(new KeyboardNodeFactory());
const model = new DiagramModel();
engine.setModel(model);
const routings = new MachineRoutings();
model.registerListener({

	linksUpdated: (lu: BaseEvent) => {

		const linksUpdatedEvent = lu as BaseEntityEvent<BaseEntity<BaseEntityGenerics>> & {

			link: MidiLinkModel;
			isCreated: boolean;
		};


		if (linksUpdatedEvent.isCreated) {

			const registered = linksUpdatedEvent.link.registerListener({

				targetPortChanged: (tpc: BaseEvent) => {
					linksUpdatedEvent.link.deregisterListener(registered);
					const targetPortChangedEvent = tpc as BaseEntityEvent<DefaultNodeModel> & {
						port: DefaultPortModel;
					};

					const portSource = linksUpdatedEvent.link.getSourcePort() as MachinePortModel;
					const sourceNode = portSource.getNode() as MachineNodeModel;
					const machineSource = sourceNode.machine as MachineSource;

					const portTarget = linksUpdatedEvent.link.getTargetPort() as MachinePortModel;
					const targetNode = portTarget.getNode() as MachineNodeModel;
					const machineTarget = targetNode.machine as MachineTarget;
					
					if (linksUpdatedEvent.link.getSourcePort().getName() === "All") {

						Object.keys(sourceNode.getMachinePorts()).forEach(key => {

							const port = sourceNode.getMachinePorts()[key];
							if (port.getName() !== "All" && !port.isIn) {

								const link = targetPortChangedEvent.port.link(port);
								engine.getModel().addAll(link);
								const sourceNode = port.getNode() as MachineNodeModel;
								const machineSource = sourceNode.machine as MachineSource;
								routings.connect(machineSource, machineTarget, port.channel, portTarget.channel, linksUpdatedEvent.link);
							}
						});

						engine.getModel().removeLink(linksUpdatedEvent.link);
					}
					else if (portSource.getLinks()[portTarget.getName()] !== undefined) {

						engine.getModel().removeLink(linksUpdatedEvent.link);
					}
					else {

						routings.connect(machineSource, machineTarget, portSource.channel, portTarget.channel, linksUpdatedEvent.link);
                    }
				}
			});
		}
		else {

			routings.disconnect(linksUpdatedEvent.link);
			// routings.disconnect(machineSource, machineTarget, portSource.channel, portTarget.channel);
		}
	}
});

interface MidispatcherState {
	readonly update: boolean;
	readonly machineFactories: { [name: string]: MachineFactory };
}

const defaultMidispatcherState: MidispatcherState = {
	update: false,
	machineFactories: {}
}

interface RefreshAction { }
interface MidiLoadedAction { inputs: WebMidi.Input[], outputs: WebMidi.Output[] }

export enum MidispatcherActionType {
	Refresh = "Refresh",
	MidiLoaded = "MidiLoaded"
}

interface SimpleAction<TType, TValue> {
	readonly type: TType;
	readonly result: TValue;
}

export type MidispatcherAction =
	| SimpleAction<MidispatcherActionType.Refresh, RefreshAction>
	| SimpleAction<MidispatcherActionType.MidiLoaded, MidiLoadedAction>

const Midispatcher: React.FunctionComponent = () => {
	const [state, dispatch] = React.useReducer(midispatcherReducer, defaultMidispatcherState);
	engine.getLinkFactories().registerFactory(new MidiLinkFactory(dispatch));

	function midispatcherReducer(state: MidispatcherState, action: MidispatcherAction) {
		switch (action.type) {
			case MidispatcherActionType.MidiLoaded:
				if (Object.keys(state.machineFactories).length === 0) {

					const refreshAction = () => dispatch({ type: MidispatcherActionType.Refresh, result: {} });
					state.machineFactories[NoteRoundRobinMachine.buildFactory(refreshAction).getName()] = NoteRoundRobinMachine.buildFactory(refreshAction);
					state.machineFactories[ThruMachine.buildFactory(refreshAction).getName()] = ThruMachine.buildFactory(refreshAction);
					state.machineFactories[KeyboardMachine.buildFactory(refreshAction).getName()] = KeyboardMachine.buildFactory(refreshAction);
					action.result.inputs.forEach(input => {
						const factory = MidiMachineSource.buildFactory(refreshAction, input, "midimessage");
						console.log(input.name + ":" + factory.getName());
						state.machineFactories[factory.getName()] = factory;
					});
					action.result.outputs.forEach(input => {
						const factory = MidiMachineTarget.buildFactory(refreshAction, input);
						state.machineFactories[factory.getName()] = factory;
					});
				}

				return {

					...state
				}
			case MidispatcherActionType.Refresh:
				return {

					...state,
					update: !state.update
				}
			default:
				return state;
		}
	}

	React.useEffect(() => {
		WebMidi.WebMidi
			.enable()
			.then(() => dispatch({ type: MidispatcherActionType.MidiLoaded, result: { inputs: WebMidi.WebMidi.inputs, outputs: WebMidi.WebMidi.outputs } }))
			.catch(err => alert(err));
	}, []);

	function onDrop<T>(event: React.DragEvent<T>) {
		var machineName = event.dataTransfer.getData("machine-name");

		const machineFactory = state.machineFactories[machineName];
		const machine = machineFactory.createMachine();
		const node = machine.buildNode();

		var point = engine.getRelativeMousePoint(event);
		node.setPosition(point);
		engine.getModel().addAll(node);
		dispatch({ type: MidispatcherActionType.Refresh, result: {} });
	}

	const trayItems = Object.keys(state.machineFactories).map(machineName =>
		<TrayItem key={machineName + "TrayItemKey"} machineFactory={state.machineFactories[machineName]}/>)

	return (
		<Workspace
			buttons={[
				<WorkspaceButton key={"saveButtonKey"}>
					Save
				</WorkspaceButton>,
				<WorkspaceButton key={"loadButtonKey"}>
					Load
				</WorkspaceButton>,
				<WorkspaceButton key={"helpButtonKey"}>
					Help
				</WorkspaceButton>
			]}>
			<S.Body>
				<S.Content>
					<Tray>
						{trayItems}
					</Tray>
					<S.Layer onDrop={onDrop}
							 onDragOver={(event) => {
							 	event.preventDefault();
							 }}>
						<Canvas>
							<CanvasWidget engine={engine} />
						</Canvas>
					</S.Layer>
				</S.Content>
			</S.Body>
		</Workspace>
	)
}

export default Midispatcher;

namespace S {
	export const Body = styled.div`
		flex-grow: 1;
		display: flex;
		flex-direction: column;
		min-height: 100%;
	`;

	export const Header = styled.div`
		display: flex;
		background: rgb(30, 30, 30);
		flex-grow: 0;
		flex-shrink: 0;
		color: white;
		font-family: Helvetica, Arial, sans-serif;
		padding: 10px;
		align-items: center;
	`;

	export const Content = styled.div`
		display: flex;
		flex-grow: 1;
	`;

	export const Layer = styled.div`
		position: relative;
		flex-grow: 1;
	`;
}