import { NodeModel, DefaultPortModel, DefaultPortModelOptions, PortModelAlignment, NodeModelGenerics } from '@projectstorm/react-diagrams';
import { BasePositionModelOptions } from '@projectstorm/react-canvas-core';
import { Machine } from './../Machines';
import { MidiLinkModel } from './Link';

export interface DefaultNodeModelOptions extends BasePositionModelOptions {

    name?: string;
    color: string;
}

export interface DefaultNodeModelGenerics extends NodeModelGenerics {

    OPTIONS: DefaultNodeModelOptions;
}

export class MachineNodeModel extends NodeModel<DefaultNodeModelGenerics> {

    protected portsIn: MachinePortModel[];
    protected portsOut: MachinePortModel[];
    readonly machine: Machine;
    private readonly requestRefresh: Function;
    constructor(requestRefresh: Function, machine: Machine, type?: string) {
        super({
            type: type ?? "default",
            name: machine.getFactory().getName(),
            color: machine.getFactory().getColor()
        });
        this.portsOut = [];
        this.portsIn = [];
        this.requestRefresh = requestRefresh;
        this.machine = machine;
    }

    getInPorts(): MachinePortModel[] {

        return this.portsIn;
    }

    getOutPorts(): MachinePortModel[] {

        return this.portsOut;
    }

    addPort(port: MachinePortModel): MachinePortModel {
        super.addPort(port);
        if (port.getOptions().in) {
            if (this.portsIn.indexOf(port) === -1) {
                this.portsIn.push(port);
            }
        } else {
            if (this.portsOut.indexOf(port) === -1) {
                this.portsOut.push(port);
            }
        }
        return port;
    }

    addMachineOutPort(label: string, channel: number): MachinePortModel {
        const p = new MachinePortModel(this.requestRefresh, {
            in: false,
            name: label,
            label: label,
            alignment: PortModelAlignment.RIGHT
        }, channel);

        return this.addPort(p);
    }

    addMachineInPort(label: string, channel: number): MachinePortModel {
        const p = new MachinePortModel(this.requestRefresh, {
            in: true,
            name: label,
            label: label,
            alignment: PortModelAlignment.LEFT
        }, channel);

        return this.addPort(p);
    }

    getMachinePorts(): { [s: string]: MachinePortModel } {
        return this.getPorts() as { [s: string]: MachinePortModel };
    }
}

export class MachinePortModel extends DefaultPortModel {
    readonly channel: number;
    readonly isIn: boolean;
    private readonly requestRefresh: Function;
    constructor(requestRefresh: Function,
        options: DefaultPortModelOptions,
        channel: number) {
        super(options)

        this.requestRefresh = requestRefresh;
        this.channel = channel;
        this.isIn = options.in!;
    }

    createLinkModel(): MidiLinkModel {

        console.log("from port");
        return new MidiLinkModel(this.requestRefresh);
    }
}